.app{box-sizing:border-box;padding:0;margin:0;font-family:"Public Sans",sans-serif;color:#000}

.auth{background:url(data:image/png;base64,) no-repeat center center fixed;background-size:cover;display:flex;justify-content:space-around;align-items:center;height:100vh;width:100vw}.auth .logo{width:50%;display:flex;justify-content:flex-end;align-items:center}.auth .logo img{width:50%;margin-right:15%}.auth .auth-section{height:100%;width:50%;display:flex;justify-content:flex-start;align-items:center}.auth .auth-section .card{background:#fff;margin-left:10%;padding:1rem 3rem;width:55%;display:flex;flex-direction:column;justify-content:flex-start;border-radius:16px;box-shadow:-1px 4px 16px 2px rgba(0,0,0,0.26);-webkit-box-shadow:-1px 4px 16px 2px rgba(0,0,0,0.26);-moz-box-shadow:-1px 4px 16px 2px rgba(0,0,0,0.26)}.auth .auth-section .card .title{font-size:4rem;font-weight:bold;margin:2rem 0 3rem 0;font-family:"Public Sans",sans-serif;color:#000}.auth .auth-section .card form{display:flex;flex-direction:column;justify-content:space-around}.auth .auth-section .card form .error{margin-top:-0.75rem;color:#ff3434;font-size:0.75rem;font-weight:700}.auth .auth-section .card form label span{display:block;font-size:1.25rem;font-weight:700;margin-top:0.5rem}.auth .auth-section .card form label input[type="email"],.auth .auth-section .card form label input[type="text"],.auth .auth-section .card form label input[type="password"]{margin:0.5rem 0 1rem 0;width:85%;font-size:1.5rem;font-family:"Public Sans",sans-serif;padding:1rem 2rem;border:3px ridge #000;border-radius:8px;font-weight:600;transition:border 300ms ease}.auth .auth-section .card form label input[type="email"]:focus,.auth .auth-section .card form label input[type="text"]:focus,.auth .auth-section .card form label input[type="password"]:focus{outline:none;border:3px ridge #8527ff}.auth .auth-section .card form .toggle{width:85%;display:flex;justify-content:flex-start;align-items:center;margin-top:0.5rem}.auth .auth-section .card form .toggle input[type="checkbox"]{cursor:pointer;height:1.5rem !important;width:1.5rem 