.nf{font-family:"Public Sans",sans-serif;display:flex;justify-content:center;align-items:center;width:100vw;height:100vh;background:url(data:image/png;base64,) no-repeat center center fixed;background-size:cover}.nf .n404{width:100%;height:100%;display:flex;flex-direction:column;justify-content:flex-start;align-items:center}.nf .n404 .picture{display:flex;justify-content:center;align-items:center;height:60%;width:50%}.nf .n404 .picture img{width:80%}.nf .n404 .right{margin-top:-5%;width:37%;display:flex;flex-direction:column;justify-content:center;align-items:center}.nf .n404 .right .text{font-size:3rem;font-weight:800;color:#8527ff}.nf .n404 .right .btns a{padding:0.75rem 1.5rem;border-radius:8px;border:3px solid #000;text-decoration:none;color:#000;font-weight:600}

.sidebar{display:flex;flex-direction:column;justify-content:space-between;font-family:"Public Sans",sans-serif;background-color:#8527ff;color:#fff;width:25%;height:calc(100vh - 4rem);padding:2rem}.sidebar .user .name{font-size:3.5rem;font-weight:800}.sidebar .user .mis{font-size:1.5rem}.sidebar .nav-links{height:45%}.sidebar .nav-links ul{height:100%;list-style:none;margin:0;padding:0;display:flex;flex-direction:column;justify-content:space-between}.sidebar .nav-links ul .active{background-color:#a25aff}.sidebar .nav-links ul li{cursor:pointer;padding:1rem 2rem;font-size:1.75rem;border-radius:8px}.sidebar .nav-links ul li:hover{background-color:#a25aff}.sidebar .logo{display:flex;justify-content:space-between;align-items:flex-end}.sidebar .logo img{width:60%}.sidebar .logo button{cursor:pointer;font-size:1rem;font-weight:700;border:none;border-radius:8px;background-color:#ff3434;color:#fff;padding:0.75rem 1.5rem;margin-bottom:0.4rem}

